import React from "react";
import "./Card.css";
import PropTypes from "prop-types";

const Card = ({ item }) => {
  const { url, lang, fcolor, scolor } = item;

  return (
    <div
      className="card"
      style={{
        background: `linear-gradient(0deg, ${fcolor} 0%, ${scolor} 100%)`,
      }}
    >
      <img src={url} alt={lang} />
      <h2>{lang}</h2>
    </div>
  );
};

Card.propTypes = {
  item: PropTypes.shape({
    lang: PropTypes.string,
    url: PropTypes.string,
    fcolor: PropTypes.string,
    scolor: PropTypes.string,
  }),
};

export default Card;
